var counterpart = require('counterpart');
var Translate   = require('react-translate-component');


counterpart.registerTranslations('ru', ru);
counterpart.registerTranslations('en', en);

counterpart.setLocale('ru');

var locale = 'ru';

function handleClick(e) {
	e.preventDefault();
	if (locale == 'ru'){
		locale = 'en';
		document.getElementById("language_select").innerHTML = "English";
		document.getElementById("language_select").setAttribute("data-hover", "Russian");
	} else {
		locale = 'ru';
		document.getElementById("language_select").innerHTML = "Russian";
		document.getElementById("language_select").setAttribute("data-hover", "English");
	}
	counterpart.setLocale(locale);
}

